.trackContainer {
  width: calc(100% - 400px);
  padding-top: 50px;
  z-index: 1;
}

.expandToggle {
  width: 100%;
}

.Tracks,
.errorMessage {
  padding: 0;
  list-style: none;
  text-align: center;
  margin: auto;
}

.centerTrack {
  position: absolute;
  top: 20%;
  padding: 15px;
}

.Tracks li,
.errorMessage {
  display: inline-block;
  width: 200px;
  position: relative;
  vertical-align: top;
  margin: 15px;
  font-family: "helvetica", san-serif;
  min-height: 200px;
  background: #fffefe;
  border: 1px solid #252727;
  text-align: left;
}
.Tracks li h2 {
  color: rgb(0, 0, 0);
  font-size: 36px;
  margin: 0;
  position: absolute;
  opacity: 0.4;
  top: 5px;
  right: 10px;
  transition: all 0.3s ease-in-out;
}
.track_title {
  font-size: 22px;
  color: #3f3f3f;
  margin-bottom: 5px;
  font-weight: bold;
}
.track_artist, .errorMessage > p {
  font-size: 18px;
  line-height: 18px;
  color: #202020;
  margin-top: 5px;
}

.Tracks li:hover h2 {
  top: 0px;
  opacity: 0.6;
}

.Tracks li:before,
.errorMessage:before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  z-index: -1;
  background: #fff;
  transform: skew(2deg, 2deg);
}
.Tracks li:after,
.errorMessage:after {
  content: "";
  position: absolute;
  width: 40%;
  height: 100%;
  left: 0;
  top: 0;
}
.Tracks .rap:before {
  background: -webkit-linear-gradient(
    to right,
    rgb(249, 0, 0),
    rgb(249, 50, 10)
  );
  background: linear-gradient(to right, rgb(249, 0, 0), rgb(249, 50, 10));
}
.Tracks .rap:after {
  background: rgba(255, 32, 32, 0.2);
}
.Tracks .rap {
  border: 2px solid rgb(255, 77, 0);
}

